<template>
  <div class="login-wrap">
    <div class="ms-login">
      <div class="ms-title">登录一下吧！</div>
      <el-form
        :model="loginForm"
        ref="login"
        label-width="0px"
        class="ms-content"
      >
        <el-form-item prop="username" class="el_form_item">
          <el-input
            v-model="loginForm.phone"
            placeholder="邮箱"
            class="input email"
          >
          </el-input>
        </el-form-item>
        <el-form-item prop="username" class="el_form_item">
          <el-input
            v-model="loginForm.phone"
            placeholder="密码"
            class="input"
          >
          </el-input>
        </el-form-item>
        <el-form-item prop="password" class="el_form_item">
          <el-input
            type="password"
            placeholder="忘记密码？那获取验证码登录吧！"
            v-model="loginForm.password"
            @keyup.enter.native="getVerifyCode()"
          >
            <el-button
              slot="prepend"
              icon="el-icon-lx-lock"
              @click="getVerifyCode()"
              >获取验证码</el-button
            >
          </el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="loginByPassword()">登录</el-button>
        </div>
        <div>
          <p class="login-tips">Tips: 请输入邮箱和验证码。</p>
          <el-button type="primary" @click="goToRegister()">注册</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  data: function () {
    return {
      loginForm: {
        phone: "",
        password: "",
      },
      param: {
        username: "admin",
        password: "123123",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    ...mapMutations(["changeLogin"]),
    goToRegister() {
      this.$router.push("/register");
    },
    loginByPassword() {
      let _this = this;
      if (this.loginForm.phone === "" || this.loginForm.password === "") {
        alert("邮箱或验证码不能为空");
      } else {
        this.tnApi.userApi
          .login(_this.loginForm)
          .then((res) => {
            if (res.status === 200) {
              console.log(res);
              _this.userToken = "Bearer " + res.token;
              // 将用户token保存到vuex中
              _this.changeLogin({ Authorization: _this.userToken });
              this.$router.push("/");
            }
          })
          .catch((error) => {
            console.log(error);
            alert("账号或密码错误");
          });
      }
    },
    loginBySms() {
      let _this = this;
      if (this.loginForm.phone === "" || this.loginForm.password === "") {
        alert("邮箱或验证码不能为空");
      } else {
        this.tnApi.userApi
          .login(_this.loginForm)
          .then((res) => {
            if (res.status === 200) {
              console.log(res);
              _this.userToken = "Bearer " + res.token;
              // 将用户token保存到vuex中
              _this.changeLogin({ Authorization: _this.userToken });
              this.$router.push("/");
            }
          })
          .catch((error) => {
            console.log(error);
            alert("账号或密码错误");
          });
      }
    },
    getVerifyCode() {
      let _this = this;
      this.tnApi.userApi.getVerifyCode(_this.loginForm.phone);
    },
  },
};
</script>

<style scoped>
.login-wrap {
  position: relative;
  width: 100%;
  height: 1000px;
  background-image: url(../assets/img/whole_bg.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.ms-title {
  width: 100%;
  line-height: 66px;
  text-align: center;
  font-size: 30px;
  color: #fff;
  border-bottom: 1px solid #ddd;
}
.ms-login {
  position: absolute;
  left: 15%;
  right: 15%;
  top: 20%;
  bottom: 20%;
  width: 70%;
  /* margin: -190px 0 0 -175px; */
  margin: auto;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
  text-align: center;
  /* overflow: scroll; */
}
.ms-content {
  padding: 30px 30px;
}
.el_form_item {
    margin-bottom: 20px;
  }
.login-btn {
  text-align: center;
}
.login-btn button {
  width: 20%;
  height: 36px;
  margin-bottom: 10px;
}
.login-tips {
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}
</style>



